.hero{
    margin: 0px 120px;
    margin-top: 70px;
}
.hero-text{
    color: white;
    font-size: 10px;
    font-weight: 500;
    line-height: 6px;
}
.hero-explore{
    display: flex;
    align-items: center;
    width: fit-content;
    margin-top: 50px;
    padding: 5px 8px;
    padding-left: 30px;
    border-radius: 60px;
    background-color: white;
    cursor: pointer;
}
.hero-explore p{
    color: #000;
    font-size: 20px;
    font-weight: 800;
}
.hero-dot-play{
    margin-top: 120px;
    display: flex;
    justify-content: space-between;
}
.hero-dots{
    display: flex;
    align-items: center;
    gap: 23px;
    list-style: none;
}
.hero-dot{
    width: 15px;
    height: 15px;
    background: #fff;
    border-radius: 7.5px;
    cursor: pointer;
}
.hero-dot.orange{
    background: orangered; 
}
.hero-play{
    display: flex;
    align-items: center;
    gap: 30px;
}
.hero-play p{
    color: #fff;
    font-size: 19px;
}
.hero .hero-text p{
    font-size: 20px;
    margin-top: 15px;
    font-style: italic;
}
.hero .hero-explore p{
   font-style: italic;
}

@media screen and (max-width:400px) {
    .hero-explore{
        text-align: left !important;
        height: 30px;
    }
    .hero-explore p{
        font-size: 12px;
    }
    .hero-explore img{
        height: 40px;
    }
}