.nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 40px 100px;
    color: white;
}
.nav-logo{
    font-family: outfit;
    font-size: 42px;
    font-weight: 500;
}
.nav-menu{
    display: flex;
    align-items: center;
    list-style: none;
    font-size: 18px;
    gap: 10px;
}
.nav-contact{
    border-radius: 50px;
    padding: 6px 18px;
    background: white;
    color: black;
}

@media screen and (max-width:510px) {
    .nav-menu{
        display: inline-block;
        text-align: center;
    }
    .nav-logo{
        font-size: 20px;
        text-align: center;
    }
}